
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: "LNavFooter",
components: {
Tabbar,
TabbarItem
},
data() {
return {};
},
computed: {
isShowFooterMenu() {
return !this.$route.meta.hiddenFooterMenu;
}
},
}



